<template id="">
  <div class="row" v-bind:class="{'head': header}">
    <div class="id">
      {{data.id}}
    </div>
    <div class="seperator"></div>
    <div class="name hide">
      {{translate(data.name)}}
    </div>
    <div class="seperator"></div>

    <div class="value" >
      <a v-bind:class="{'hide':this.edit}"  v-on:click="go_edit">{{this.input}}</a>
      <input :class="{'hide':!this.edit}"
        :value="this.input"
        type="text"
        ref="input"
        v-on:keyup.enter="enter"
        >
    </div>
    <div class="seperator"></div>

    <div class="comment">
      {{translate(data.comment)}}
    </div>

  </div>
</template>
<script type="text/javascript">
import axios from 'axios'
import vue from 'vue'


  export default{
    name: 'valuerow', components: {  },
    props:{data:Object, header:Boolean},
    methods:{
      translate(text){
        return vue.$translate(text)
      },
      go_edit(){
        if(this.input == '')
            this.input = this.data.value

        this.edit = !this.header;
        this.$emit('edit', this.edit)
        if(vue.$keyboardenter)
          vue.$keyboardenter()

        vue.$openkeyboard(this.input.toString(), (input) => this.input=input, () => this.enter() )

      },
      enter(){
        this.edit = false
        this.$emit('edit', this.edit)

        this.$forceUpdate()

        axios({
          method: 'post',
          url: vue.$url + '/setvalue',
          data: {
            id: this.data.id,
            value:   this.input
          }
        }).then(() => {console.log('acc', this.edit)}).catch(()=>{});


      }
    },
    data(){
      return{
        edit: false,
        data_value: '',
        input: ''
      }
    },
    created(){
      this.input = this.data.value
    },
    updated(){
      this.$nextTick(()=> {
        if(this.edit)
          this.$refs.input.focus()
      });
    }
  }

</script>
<style media="screen">
  .row{
    display: flex;
    flex-direction: row;

    font-size: 20pt;
  }
  .row.head{
    font-weight: bold;
  }
  .row .id{
    flex: 1 0 0;
  }
  .row .name{
    flex: 3 0 0
  }
  .row .value{
    flex: 2 0 0;
    display: flex;

  }
  .row .value a{
    margin: auto;
    width: 130pt;

  }
  .row .value input{
    flex: 1 0 0;
    margin: 0 2pt;
  }
  .row .comment{
    flex: 3 0 0 ;
  }
  .seperator{
    flex: 0 0 2pt;
    background-color: #293133;

  }
  .keyboard{
    position: fixed;
    bottom: 0;
    z-index: 10;
    left: 0;
  }
</style>
