<template id="">
  <div class="tank" v-on:click="controls_visible = !controls_visible"
  v-bind:style="
  'width:' + box.width +
  'px; height:' + box.height +
  'px; top:' + box.top +
  'px; left:' + box.left +'px;'"
  v-bind:class="{'auto_ein':this.status==3, 'hand_ein': this.status==1, 'hand_aus':this.status==0}"
  >
    <div class="water" v-bind:style="'flex: 0 0 ' + level + '%;'">
      <a>
        {{level + '%'}}
      </a>
    </div>
    <p class="pipetext" v-bind:style="'height: ' + (100 - level) + '%;'">{{text}}</p>
  <controlelement
  style="
    position: absolute;
    top: 135px;

  "
  v-bind:controls_visible="controls_visible"
  v-on:set-value="this.set_value"
  v-bind:control_box="control_box"
  class="tankcontrol"
  />
</div>
</template>
<script type="text/javascript">
import controlelement from './controlelement.vue'
import axios from 'axios'
import vue from 'vue'

export default{
  name: 'tank',
  components:{
    controlelement
  },
  props: { text : String, box:Object, data_id:Number, control_box:String, handid:Number, handbetrieb:Number, requestid:Number },
  methods:{
    set_value(status){
      this.status = status

      axios({
        method: 'post',
        url: vue.$url + '/setvalue',
        data: {
          id: this.handid,
          value: status && !(status==2)
        }
      }).then(() => {this.status = status}).catch(()=>{})
      axios({
        method: 'post',
        url: vue.$url + '/setvalue',
        data: {
          id: this.handbetriebid,
          value: !(status==2)
        }
      }).then(() => {console.log('acc')}).catch(()=>{})

    }
  },
  data(){
    return{
      pipe_filled: this.pipe_before_filled && this.ventil_before_open,
      box_top: this.box.top,
      box_left: this.box.left,
      box_weight: this.box.weight,
      box_height: this.box.height,
      status: 2,
      controls_visible: false,

      level: 40.7
    }
  },
  created(){
    setInterval(() => {
      axios({
        method: 'post',
        url: vue.$url + '/getvalue',
        data: {
          id: this.data_id
        }
      }).then((e) => {
        this.level = Math.round(e.data.value*100)/100;
      }).catch(()=>{});

      axios({
        method: 'post',
        url: vue.$url + '/getvalue',
        data: {
          id: this.requestid
        }
      }).then((e) => {
        if(this.status == 2 || this.status == 3)
          this.status = 2 + e.data.value

      }).catch(()=>{});
    }, 500)

  }
}
</script>
<style media="screen">
.tank{
  border: solid black 2pt;
  border-top: none;
  position: absolute;
  display: flex;
  flex-direction: column-reverse;

}
.tank.auto_ein{
  border-bottom-color: green;

}
.tank.hand_aus{
  border-color: yellow;
  border-bottom-color: black;
}
.tank.hand_ein{
  border-color: yellow;
  border-bottom-color: green;
}


.tank .water{
  background-color: #00FFFF;
}
.tank .water a{
  background-color: white;
  font-weight: bold;
  margin-top: -14pt;
  margin-left: -14pt;
  position: absolute;
}
</style>
