<template id="">
  <div class="err_dialog"
  v-bind:class="{'hide':!this.show}"
  v-bind:style="
  'width:' + box.width +
  'px; height:' + box.height +
  'px; top:' + box.top +
  'px; left:' + box.left +'px;'"
  >
    <div class="img red">

    </div>
    <div class="text_wrapper">
      <p>{{translate('Error')}}</p>
      <div class="horizontal_wrapper">
        {{translate(text)}}
        <button type="button" name="button" v-on:click="close">{{translate('OK')}}</button>
      </div>
    </div>
  </div>
</template>
<script type="text/javascript">
import vue from 'vue'
import axios from 'axios'

export default{
  name:'err_dialog',
  props:{
    visible: Boolean,
    error_id: Number,
    box: Object,
    text: String
  },
  methods:{
    translate(text){
      return vue.$translate(text);
    },
    close(){
      this.show = false;
      this.restart = 0.0;
    }
  },
  created(){
    this.show = this.visible;
    setInterval(() => {
      axios({
        method: 'post',
        url: vue.$url + '/getvalue',
        data: {
          id: this.error_id
        }
      }).then((e) => {
        this.error = e.data.value;
        this.restart = this.restart + 0.5;
        if(this.restart >= 6.0 && this.error)
          this.show = true;
      }).catch(()=>{});
    }, 500)

  },
  data(){
    return{
      error: 0,
      restart: 6.0,
      show: false
    };
  }
}
</script>
<style media="screen">
.err_dialog{
  position: absolute;
  background-color: #ccc;
  display: flex;
  flex-direction: row;
  z-index: 1000;
  border: black 2pt solid;
}
.img.red{
  background-color: red;
  flex: 1 0 0;
}
.text_wrapper{
  flex: 3 0 0;
}
.horizontal_wrapper{
  display: flex;
  flex-direction: column;
}
.err_dialog p {
  font-size: 22pt;
  margin: 3pt auto;
}
</style>
